// app/home/page.tsx
"use client";
import styles from "./editorStyle.module.css";
import { Canvas } from "reaflow";

import { useJsonGraph } from "@/hooks/useJsonGraph";
import { useMemo } from "react";
import JSONEditor from "@/components/editors/JsonEditor";

import { CustomNode } from "@/components/customnode/customNode";

export default function HomePage() {
  const { nodes, edges, handleChange } = useJsonGraph();

  const reaflowEdges = useMemo(() => {
    return edges.map((edge) => ({
      id: edge.id, // ✅ Redan rätt
      from: edge.from, // ✅ Redan rätt
      to: edge.to, // ✅ Redan rätt
    }));
  }, [edges]);

  const reaflowNodes = useMemo(() => {
    return nodes.map((node) => ({
      id: node.id,
      data: node,
    }));
  }, [nodes]);

  return (
    <div className={styles.editor}>
      <JSONEditor handleChange={handleChange} />
      <Canvas
        nodes={reaflowNodes}
        edges={reaflowEdges}
        direction="RIGHT"
        layoutOptions={{
          "elk.algorithm": "layered",
          "elk.direction": "RIGHT",
          "elk.spacing.nodeNode": "300",
          "elk.layered.spacing.nodeNodeBetweenLayers": "500",
        }}
        node={(nodeProps) => <CustomNode {...nodeProps} />}
      />
    </div>
  );
}
