import { Node, NodeProps } from "reaflow";
import NodeContainer from "../NodeContainer/NodeContainer";
import PrimativeValueList from "../Values/PrimativeValueList/PrimativeValueList";
import PrimativeValue from "../Values/PrimativeValue/primativeValue";
import BreakOutsList from "../NodeContainer/BreakOuts/BreakOutList/BreakOutsList";
import BreakOutItem from "../NodeContainer/BreakOuts/BreakOutItem/BreakOutItem";
import { Fragment, useLayoutEffect, useRef, useState } from "react";

export function CustomNode(props: NodeProps) {
  const ref = useRef<HTMLDivElement>(null);
  const [size, setSize] = useState({ width: 300, height: 200 });
  const data = props.properties?.data;

  useLayoutEffect(() => {
    if (!ref.current) return;

    const resizeObserver = new ResizeObserver(([entry]) => {
      const { width, height } = entry.contentRect;
      setSize({ width, height });
    });

    resizeObserver.observe(ref.current);
    return () => resizeObserver.disconnect();
  }, []);
  return (
    <Node
      {...props}
      style={{
        opacity: 0,
      }}
      width={300}
      height={450}
    >
      <foreignObject width={300} height={450}>
        <div
          style={{
            position: "fixed",
            width: "100%",
            height: "100%",
            pointerEvents: "auto",
            backgroundColor: "white", // tillfälligt för att se
            border: "1px solid #ccc",
          }}
        >
          <NodeContainer label={data.label} type="object">
            <PrimativeValueList>
              {data.data?.map((item, index) => (
                <PrimativeValue key={index} keyName={item.key} value={item.value} dataType={item.dataType} />
              ))}
            </PrimativeValueList>
            <BreakOutsList>
              {data.breakouts?.map((item, index) => (
                <Fragment key={index}>
                  <BreakOutItem item={item} />
                </Fragment>
              ))}
            </BreakOutsList>
          </NodeContainer>
        </div>
      </foreignObject>
    </Node>
  );
}
